.due-time { 
    position: relative;
    width: 100%;
    height: 35px;
}
.due-time-symbol {
    margin-right: 7px;
}
.time-choice-container{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    top: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 90%;
    font-size: 20px;
    padding: 10px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#d3d3d3));
    background-image: -webkit-linear-gradient(#fff, #d3d3d3);
    background-image: -o-linear-gradient(#fff, #d3d3d3);
    background-image: linear-gradient(#fff, #d3d3d3);
    display: block;
    z-index: 1000;
    border-radius: 10px;
}
.time-choice-hour > span {
    display: inline-block;
    width: calc(100% / 6);
    height: 20px;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    font-weight: 550;
}
.time-choice-min > span {
    display: inline-block;
    width: calc(100% / 6);
    height: 15px;
    font-size: 13px;
    line-height: 15px;
    text-align: center; 
}
.time-choice-hour .active,
.time-choice-min .active {
    background: pink;
    border-radius: 4px;
}
.time-prefix {
    font-size: 10px;
    font-weight: normal;
}


